@import url("https://fonts.googleapis.com/css2?family=Merriweather:wght@400;700&family=Montserrat&family=Open+Sans&family=Poppins:wght@500&family=Roboto&display=swap");
/* font-family: 'Merriweather', serif;
font-family: 'Montserrat', sans-serif;
font-family: 'Open Sans', sans-serif;
font-family: 'Poppins', sans-serif;
font-family: 'Roboto', sans-serif; */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.form-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}
.registration-form {
  border: 1px solid gray;
  padding: 20px;
  width: 30%;
  font-family: "Merriweather", serif;
}
.registration-form p,
.registration-form a {
  font-family: "Merriweather", serif;
  font-size: 14px;
  text-decoration: none;
}
/* Loader */
.spinner {
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 9999;
}

/* Home page design*/
.main {
  padding: 7px;
  height: 100vh;
}
.main .layout {
  display: flex;
}
.main .layout .sidebar {
  min-height: 100%;
  width: 330px;
  border-radius: 5px;
  background-color: #5f27cd;
  box-shadow: 0 0 2px gray;
  margin-right: 20px;
  color: #fff;
}
.main .layout .sidebar .logo img {
  height: 70px;
}
.main .layout .sidebar .logo {
  font-size: 22px;
  margin: 20px 0;
  text-align: center;
  font-weight: bolder;
  font-family: "Montserrat", sans-serif;
}
.main .layout .content {
  width: 100%;
  height: 100%;
}
.main .layout .content .header {
  height: 10vh;
  margin-bottom: 20px;
  box-shadow: 0 0 2px gray;
  background-color: #fff;
}
.main .layout .content .body {
  height: 85vh;
  margin-bottom: 20px;
  box-shadow: 0 0 2px gray;
  background-color: #fff;
}
/* Menu  */
.main .layout .sidebar .menu-item {
  margin: 8px 10px;
  padding: 10px 10px;
  border-radius: 7px;
}
.main .layout .sidebar .menu .menu-item a {
  text-decoration: none;
  color: #fff;
  font-size: 20px;
}
.main .layout .sidebar .menu .menu-item i {
  font-size: 20px;
  margin-right: 9px;
}
.main .layout .sidebar .active {
  background-color: #4a69bd;
}

/* header content  */
.header {
  align-items: center;
  height: 50px;
}
.header-content {
  display: flex;
  height: 100%;
  align-items: center;
  justify-content: flex-end;
  margin: 0 15px;
}
.header-content .header-cnt {
  margin-right: 12px;
}
.header-content .header-cnt i {
  font-size: 20px;
  margin-right: 5px;
}
.header-content a {
  text-decoration: none;
  font-size: 20px;
  margin: 0 10px;
  color: #5f27cd;
}

/* Apply doctot Form  */
.form-btn {
  width: 250px !important;
  margin-top: 30px !important;
}

.card .card-header {
  background-color: #4a69bd;
  color: #fff;
  font-size: 18px;
  font-family: "Poppins", sans-serif;
}
.card .card-body {
  background-color: lavender;
}
.card .card-body p {
  font-size: 15px;
  font-family: "Roboto", sans-serif;
}

.container .availabal {
  width: 350px;
}
